// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import $ from 'jquery'
import './foundation'
import './assets/css/_main.scss'
import Import from './components/Import'
import Vue from 'vue'
import Masonry from 'masonry-layout'
// import router from './router'
import App from './App'

/* eslint-disable */

new Vue({
  el: '#app',
  // router,
  components: {App},
  template: '<App/>'
})

var onImgLoad = function (selector, callback) {
  $(selector).each(function () {
    if (this.complete || /*for IE 10-*/ $(this).height() > 0) {
      callback.apply(this);
    }
    else {
      $(this).on('load', function () {
        callback.apply(this);
      });
    }
  });
};

$(document).foundation()
window.onload = function () {
  $(document).ready(function () {
    if ($('.grid').length) {
      var elem = document.querySelector('.grid')
      var img = $('img')
      if (img.length > 0) {
        onImgLoad('img', function () {
          new Masonry(elem, {
            itemSelector: '.grid-item'
          });
        });
      } else {
        new Masonry(elem, {
          itemSelector: '.grid-item'
        });
      }
    }
  });
};


if ($('.sub-fact').length > 0) {
  $('.comment-section-container').height($('.fact-response').height() - $('.comment-section-form').height())
  var fontSize = 0
  var width = $('.fact-detail .content').width()
  var height = $('.fact-detail .content').height()
  while (height < width) {
    fontSize++
    $('.fact-detail .content').css('font-size', fontSize)
    width = $('.fact-detail .content').width()
    height = $('.fact-detail .content').height()
  }
  $('.fact-detail .content').css('font-size', fontSize-1)
  $('.fact-detail').height($('.fact-response').height() - 30 - $('.fact-footer').height())
}

// more click
$('.card-profile-stats-more-link').click(function (e) {
  e.preventDefault()
  if ($(".card-profile-stats-more-content").is(':hidden')) {
    $('.card-profile-stats-more-link').find('i').removeClass('fa-angle-down').addClass('fa-angle-up')
  } else {
    $('.card-profile-stats-more-link').find('i').removeClass('fa-angle-up').addClass('fa-angle-down')
  }
  $(this).next('.card-profile-stats-more-content').slideToggle()
})

if ($('#app').length) {
  new Vue({
    el: '#app',
    components: {Import: Import},
  })
}

var bg_classes = [
  'circles-dark',
  'circles-light',
  'dust_scratches',
  'gaming-pattern',
  'halftone-yellow',
  'hypnotize',
  'intersection',
  'memphis-colorful',
  'organic-tiles',
  'playstation-pattern',
  'pow-star',
  'regal',
  'round',
  'sakura',
  'vertical-waves',
  'vintage-concrete',
  'whirlpool'
]


var rand = bg_classes[Math.floor(Math.random() * bg_classes.length)];
$('#main-site').each(function () {
  var rand = bg_classes[Math.floor(Math.random() * bg_classes.length)];
  $(this).addClass('default');
  $(this).addClass(rand);
})

var index = 1
$('.fact-child .callout').each(function () {
  $(this).attr('data-content', index)
  index ++;
});

