<template>
  <div>
    <div class="grid-x grid-padding-x small-up-1 medium-up-2 input-control">
      <div class="cell">
        <input id="check_all" type="checkbox" @click="selectAll" v-model="allSelected">
        <label for="check_all">Check all</label>
      </div>
      <div class="large-12 text-right cell">
        <button class="button small" @click="importList(listAll)">IMPORT ALL</button>
        <button class="button small" @click="importList(selected)">IMPORT SELECTED</button>
      </div>
    </div>
    <div class="import-data">
      <table class="fixed_headers">
        <thead class="import-table-header">
        <tr>
          <th width="50"></th>
          <th width="50">ID</th>
          <th width="">Title</th>
          <th width="50">Action</th>
          <th width="50">Status</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="post in listAll" :key="post.id">
          <td><input type="checkbox" v-model="selected" @click="select" :value="post"></td>
          <td>{{ post.id }}</td>
          <td>{{ post.name }}</td>
          <td>
            <button class="button" @click="importFile(post)">Import</button>
          </td>
          <td>{{ post.status }}</td>
        </tr>
        </tbody>
      </table>
    </div>
    <input type="file" @change="getFile($event)">
  </div>
</template>

<script>
/* eslint-disable */
  import axios from 'axios'
  import NProgress from 'nprogress'

  export default {
    name: 'import',
    data() {
      return {
        listAll: [],
        allSelected: false,
        selected: []
      }
    },

    methods: {
      getFile(event) {
        var reader = new FileReader()
        reader.onload = function (e) {
          var index = 1;
          this.listAll = JSON.parse(e.target.result)
          this.listAll.forEach(post => {
            this.$set(post, 'id', index)
            this.$set(post, 'checked', false)
            this.$set(post, 'status', 'Pending')
            index++
          })
        }.bind(this)
        reader.readAsText(event.target.files[0])
      },

      selectAll: function () {
        this.selected = [];

        if (!this.allSelected) {
          for (var post in this.listAll) {
            this.selected.push(this.listAll[post]);
          }
        }
      },

      select: function () {
        this.allSelected = false;
      },

      importFile(data) {
        NProgress.start();
        axios.post('http://127.0.0.1:8000/manager/import/', data)
          .then(response => {
            this.$set(this.listAll[data.id - 1], 'status', response.data)
            console.log(response.data)
            NProgress.done();
          })
          .catch(error => {
            NProgress.done();
            console.log('Error!')
          })
      },

      importList(list) {
        list.forEach(data => {
          this.importFile(data)
        })
      }
    }
  }

</script>



